
*{
  /* border: 1px solid red; */
  margin: 0px;
}



body {
  margin: 0;
  font-family: 'Alatsi', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("assets/bg.png");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
  height: 100vh;
  align-items: center;
  display: flex;
  justify-content: center;
  color: white;
  
}



.details_container{
  background-color: #6B6B6B;
  border-radius: 70px;
  height: 80vh;
  width: 90vw;
  display: flex;
  /* justify-content: space-between; */
  /* padding-left: 50px; */
}

.informations{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 30%;
  height: 80vh;

}

.informations > *{
  padding-top: 10px;
}

.nume{
  /* font-size: 32px; */
  font-size:5vh;
  font-weight: 100;
  padding: 0px;
}

.job{
  font-size: 4vh;
  font-weight: 100;
  padding-top: 0px;
}

.socials{
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.socials > *{
  border: 0.4vh solid white;
  border-radius: 100px;
  height: 4.7vh !important;
  width: 4.7vh !important;
  margin: 0.8vh;
}

.cv_download{
  background-color: transparent;
  border: 0.4vh solid white;
  border-radius: 100px;
  margin: 0;
  padding:10px 15px;
  text-align: inherit;
  font: inherit;
  font-size: 2.4vh;
  color: white;
  margin-top: 10px;
  text-decoration: none;
  
}

.img_profil_details{
  height:35%;
  width: auto;
  
  border-radius: 10000px;
  border: 1.4vh solid #262626;
  padding-top: 0px;
  
}
