.cards::-webkit-scrollbar {
  display: none;
}

.cards {
  background-color: #262626;
  width: 80%;
  border-radius: 70px;
  overflow-y: scroll;
  scrollbar-color: #6969dd #e0e0e0;
  scrollbar-width: thin;

  .card {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 80vh;

    .titlu-card {
      font-size: 6vh;
      height: 15%;
      text-align: center;
      padding-top: 2vh;
    }

    .continut{
      height: 80%;
      width: 100%;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
    }
    

    i {
      font-size: 20px;
      height: 7%;
    }
  }
}

.card-prezentare {
  .continut {
    flex-direction: row !important;
    padding-top: 5vh;
  }

  .img_profil_card {
    height: 40vh;
    width: 40vh;
    border-radius: 1000px;
    border: 1.5vh solid #6b6b6b;
    padding-top: 0px;
    margin-right: 4vw;
  }

  .text {
    max-width: 35%;
    text-align: left;

    h1 {
      font-size: 5vh;
    }

    .prezentare {
      font-size: 3vh;
    }
  }
}

